.dialogsWrapper {
    display: flex;
    background-color: #fff;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: flex-start;
    margin: auto;
    width: 100%;
    height: fit-content;
}

.dialogsContainer {
    height: 500px;
    width: 30%;
    border: 1px solid grey;
    margin: 20px 0 0 20px;
    background-color: rgb(239, 239, 239);
    padding: 10px;
}

.dialog {
    padding: 10px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: left;
    overflow: hidden;
    max-width: 100%;
    margin: 3px;
}

.activeDialog {
    padding: 10px;
    background-color: rgb(5, 85, 206);
    display: flex;
    flex-direction: row;
    justify-content: left;
    margin: 3px;
    max-width: 100%;
}

.dialog:hover {
    background-color: #fff;
}

.activeDialog:hover {
    filter: brightness(1.1);
}

.dialogName{
    font-weight: 600;
    font-size: 20px;
}

.messagesContainer {
    padding: 10px;
    flex-grow: 1;
    overflow: scroll;
    border: 1px solid grey;
    height: 500px;
    margin: 20px 20px 0 2px;
}
.message {
    padding: 10px;
}

.message img {
    padding: 5px;
    vertical-align: middle;
}

