.dialogsFormContainer {
    width: 100%;
    display: flex;
    align-items: center;
    height: 100px;
    margin: 0;
    padding: 0 20px 0;
}

.dialogsForm {
    width: 100%;
    display: flex;
    align-items: center;
}

.messageTextarea {
    resize: none;
    width: 100%;
    flex-grow: 1;
}

.sendButton {
    background-color: rgb(5, 85, 206);
    border-radius: 8px;
    height: 30px;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    margin: 5px;
}

.sendButton:hover {
    filter: brightness(1.2);
}

.dialogPhoto {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin: 5px;
}

@media (max-width: 700px) {
    .dialogsContainer {
        width: 100%;
        border: 1px solid grey;
        margin: 0;
        background-color: rgb(239, 239, 239);
        padding: 10px;
    }
    .messagesContainer {
        padding: 10px;
        flex-grow: 1;
        overflow: scroll;
        border: 1px solid grey;
        height: 400px;
        margin: 0;
    }
}


.back{
    color: rgb(5, 85, 206);
    font-size: 20px;
}